/* .cube.show-front  { transform: translateZ(-100px) rotateY(   0deg); }
.cube.show-right  { transform: translateZ(-100px) rotateY( -90deg); }
.cube.show-back   { transform: translateZ(-100px) rotateY(-180deg); }
.cube.show-left   { transform: translateZ(-100px) rotateY(  90deg); }
.cube.show-top    { transform: translateZ(-100px) rotateX( -90deg); }
.cube.show-bottom { transform: translateZ(-100px) rotateX(  90deg); } */

.cube-face--front  { -webkit-transform: rotateY(  0deg) translateZ(30px); transform: rotateY(  0deg) translateZ(30px); }
.cube-face--right  { -webkit-transform: rotateY( 90deg) translateZ(30px); transform: rotateY( 90deg) translateZ(30px); }
.cube-face--back   { -webkit-transform: rotateY(180deg) translateZ(30px); transform: rotateY(180deg) translateZ(30px); }
.cube-face--left   { -webkit-transform: rotateY(-90deg) translateZ(30px); transform: rotateY(-90deg) translateZ(30px); }
.cube-face--top    { -webkit-transform: rotateX( 90deg) translateZ(30px); transform: rotateX( 90deg) translateZ(30px); }
.cube-face--bottom { -webkit-transform: rotateX(-90deg) translateZ(30px); transform: rotateX(-90deg) translateZ(30px); }

/* top level */
.frontPiece.topPiece.leftPiece {-webkit-transform: translateY(-63px) translateX(-63px) translateZ(63px);transform: translateY(-63px) translateX(-63px) translateZ(63px);}
.frontPiece.topPiece.midVertPiece {-webkit-transform: translateY(-63px) translateX(0px) translateZ(63px);transform: translateY(-63px) translateX(0px) translateZ(63px);}
.frontPiece.topPiece.rightPiece {-webkit-transform: translateY(-63px) translateX(63px) translateZ(63px);transform: translateY(-63px) translateX(63px) translateZ(63px);}

.midDepthPiece.topPiece.leftPiece {-webkit-transform: translateY(-63px) translateX(-63px) translateZ(0px);transform: translateY(-63px) translateX(-63px) translateZ(0px);}
.midDepthPiece.topPiece.midVertPiece {-webkit-transform: translateY(-63px) translateX(0px) translateZ(0px);transform: translateY(-63px) translateX(0px) translateZ(0px);}
.midDepthPiece.topPiece.rightPiece {-webkit-transform: translateY(-63px) translateX(63px) translateZ(0px);transform: translateY(-63px) translateX(63px) translateZ(0px);}

.backPiece.topPiece.leftPiece {-webkit-transform: translateY(-63px) translateX(-63px) translateZ(-63px);transform: translateY(-63px) translateX(-63px) translateZ(-63px);}
.backPiece.topPiece.midVertPiece {-webkit-transform: translateY(-63px) translateX(0px) translateZ(-63px);transform: translateY(-63px) translateX(0px) translateZ(-63px);}
.backPiece.topPiece.rightPiece {-webkit-transform: translateY(-63px) translateX(63px) translateZ(-63px);transform: translateY(-63px) translateX(63px) translateZ(-63px);}

/* mid level */
.frontPiece.midHorPiece.leftPiece {-webkit-transform: translateY(0px) translateX(-63px) translateZ(63px);transform: translateY(0px) translateX(-63px) translateZ(63px);}
.frontPiece.midHorPiece.midVertPiece {-webkit-transform: translateY(0px) translateX(0px) translateZ(63px);transform: translateY(0px) translateX(0px) translateZ(63px);}
.frontPiece.midHorPiece.rightPiece {-webkit-transform: translateY(0px) translateX(63px) translateZ(63px);transform: translateY(0px) translateX(63px) translateZ(63px);}

.midDepthPiece.midHorPiece.leftPiece {-webkit-transform: translateY(0px) translateX(-63px) translateZ(0px);transform: translateY(0px) translateX(-63px) translateZ(0px);}
.midDepthPiece.midHorPiece.midVertPiece {-webkit-transform: translateY(0px) translateX(0px) translateZ(0px);transform: translateY(0px) translateX(0px) translateZ(0px);}
.midDepthPiece.midHorPiece.rightPiece {-webkit-transform: translateY(0px) translateX(63px) translateZ(0px);transform: translateY(0px) translateX(63px) translateZ(0px);}

.backPiece.midHorPiece.leftPiece {-webkit-transform: translateY(0px) translateX(-63px) translateZ(-63px);transform: translateY(0px) translateX(-63px) translateZ(-63px);}
.backPiece.midHorPiece.midVertPiece {-webkit-transform: translateY(0px) translateX(0px) translateZ(-63px);transform: translateY(0px) translateX(0px) translateZ(-63px);}
.backPiece.midHorPiece.rightPiece {-webkit-transform: translateY(0px) translateX(63px) translateZ(-63px);transform: translateY(0px) translateX(63px) translateZ(-63px);}

/* bottom level */
.frontPiece.bottomPiece.leftPiece {-webkit-transform: translateY(63px) translateX(-63px) translateZ(63px);transform: translateY(63px) translateX(-63px) translateZ(63px);}
.frontPiece.bottomPiece.midVertPiece {-webkit-transform: translateY(63px) translateX(0px) translateZ(63px);transform: translateY(63px) translateX(0px) translateZ(63px);}
.frontPiece.bottomPiece.rightPiece {-webkit-transform: translateY(63px) translateX(63px) translateZ(63px);transform: translateY(63px) translateX(63px) translateZ(63px);}

.midDepthPiece.bottomPiece.leftPiece {-webkit-transform: translateY(63px) translateX(-63px) translateZ(0px);transform: translateY(63px) translateX(-63px) translateZ(0px);}
.midDepthPiece.bottomPiece.midVertPiece {-webkit-transform: translateY(63px) translateX(0px) translateZ(0px);transform: translateY(63px) translateX(0px) translateZ(0px);}
.midDepthPiece.bottomPiece.rightPiece {-webkit-transform: translateY(63px) translateX(63px) translateZ(0px);transform: translateY(63px) translateX(63px) translateZ(0px);}

.backPiece.bottomPiece.leftPiece {-webkit-transform: translateY(63px) translateX(-63px) translateZ(-63px);transform: translateY(63px) translateX(-63px) translateZ(-63px);}
.backPiece.bottomPiece.midVertPiece {-webkit-transform: translateY(63px) translateX(0px) translateZ(-63px);transform: translateY(63px) translateX(0px) translateZ(-63px);}
.backPiece.bottomPiece.rightPiece {-webkit-transform: translateY(63px) translateX(63px) translateZ(-63px);transform: translateY(63px) translateX(63px) translateZ(-63px);}

